.champion-detail h2 {
  color: white;
}
.champion-detail a{
    text-decoration: none;
    border: 2px solid white;
    border-radius: 5px;

}
.champion-detail a:hover{
    background-color: rgba(255, 255, 255, 0.459);
}

.champion-detail {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  background-color: black;
  margin: 0 auto;
  border: 1px solid rgba(58, 57, 57, 0.938);
}
.champion-detail p {
  color: rgba(255, 255, 255, 0.658);
  width: 69%;
}
.champion-detail p:hover {
  border: 1px solid white;
  cursor: pointer;
  color: white;
  border: 1px solid white;
}
.img-container img {
  width: 40%;
  height: auto;
  margin-left: 5px;
  border: 1px solid rgba(255, 255, 255, 0.623);
}
.img-container img:hover {
  cursor: pointer;
  border: 4px solid rgb(255, 255, 255);
}

.champion-detail .img-container {
  width: 70%;
  display: flex;
  overflow-x: scroll;
}

.img-container .active {
  width: 100%;
  height: auto;
}
