.champion-info-container {
  width: 100%;
  padding: 14px;
  box-shadow: 2px 2px 5px #e9e9e9;
  transition: 0.6s;
  background-color: grey;
}
.champion-info-container:hover {
  box-shadow: 2px 2px 10px #999;
  cursor: pointer;
  background-color: rgb(231, 230, 230);
}
.champion-info-container img {
  width: 100%;
  height: auto;
}
.champion-info-container img:hover {
  border: 3px solid grey;
}
.champion-info-container p {
  max-height: 110px;
  overflow-y: scroll;
  margin: 0px;
  margin-top: 15px;
}
.champion-info-container p:hover {
  background-color: antiquewhite;
  border: 1px solid rgba(0, 0, 0, 0.452);
}
.champion-info-container h3 {
  margin: 0px;
}

.champions-container {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
}

button {
  width: 40%;
  height: 10%;
  background-color: rgb(233, 228, 225);
  border-radius: 10%;
  font-size: 20px;
  text-align: center;
}
button:hover {
  background-color: rgb(175, 202, 206);
}
