.detail {
  height: 60vh;
  width: 50%;
  background-color: white;
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  box-shadow: 2px 2px 5px #eee;
  display: none;
}

.active {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}
.active img {
  width: 60%;
  height: auto;
}
.active button {
  position: relative;
  top: -87%;
  right: -45%;
  width: 5%;
  height: 8%;
}
.active p {
  width: 80%;
}
