.App {
  text-align: center;
}
* {
  box-sizing: border-box;
}
body {
  background-color: rgb(19, 7, 5);
}

.champions-container {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  width: 80%;
  column-gap: 30px;
  row-gap: 30px;
  margin: 20px auto;
}
.App h1 {
  margin-top: 30px;
  color: white;
}

.App a {
  text-decoration: none;
  color: black;
}

.meme {
  position: fixed;
  left: 30%;
  top: 30%;
  color: black;
  width: 40%;
  height: 40%;

  background-color: white;
}
